import * as React from 'react'
import { Router, Switch, Route } from 'react-router-dom'
import PageWrapper, { AdaptMode } from '@public/components/PageWrapper'

import history from '@src/history'
import { HOOKS, RENDERPROPS, HOME, MAP } from '@constants/path'

const Home = React.lazy(() => import('@routes/Home'))
const HooksDemo = React.lazy(() => import('@routes/HooksDemo'))
const MapDemo = React.lazy(() => import('@routes/MapDemo'))
const RenderPropsDemo = React.lazy(() => import('@routes/RenderPropsDemo'))
const ErrorPage = React.lazy(() => import('@public/components/ErrorPage'))
class App extends React.Component {
  render() {
    return (
      <PageWrapper mode={AdaptMode.全屏}>
        <div className="main">
          <Router history={history}>
            <React.Suspense fallback={<div>loading...</div>}>
              <Switch>
                <Route exact path={HOME} component={Home} />
                <Route path={HOOKS} component={HooksDemo} />
                <Route path={MAP} component={MapDemo} />
                <Route path={RENDERPROPS} component={RenderPropsDemo} />
                <Route component={() => <ErrorPage homeTitle="天壤-前端" />} />
              </Switch>
            </React.Suspense>
          </Router>
        </div>
      </PageWrapper>
    )
  }
}

export default App
