import * as React from 'react'
import { useState, useEffect } from 'react'

import { debounce } from 'lodash'
import { Color } from 'csstype'

export enum AdaptMode {
  全屏 = 'full-screen',
  宽度铺满 = 'overspread-x',
  高度铺满 = 'overspread-y',
}

interface IProps {
  w?: number
  h?: number
  backgroundColor?: Color
  backgroundImage?: string
  mode?: AdaptMode
  reverse?: boolean // 反向缩放
  children?: JSX.Element
}

function getSize() {
  return {
    innerHeight: window.innerHeight,
    innerWidth: window.innerWidth,
    outerHeight: window.outerHeight,
    outerWidth: window.outerWidth,
  }
}

export default function PageWrapper(props: IProps) {
  const defaultProps = {
    w: 1920,
    h: 1080,
    backgroundColor: 'transparent',
    mode: 'overspread-x',
    reverse: false,
  }

  const newProps = {
    ...defaultProps,
    ...props,
  }

  const newStyle: React.CSSProperties = {
    position: 'absolute',
    width: newProps.w + 'px',
    height: newProps.h + 'px',
    overflow: 'hidden',
    transformOrigin: '0 0',
    backgroundColor: newProps.backgroundColor,
    backgroundImage: `url(${newProps.backgroundImage || ''})`,
  }

  const [init, setInit] = useState(false)
  const [style, setStyle] = useState(newStyle)
  const wrpRef = React.useRef(null)

  // resize
  const handleResize = debounce(() => {
    const { w, h, reverse, mode } = newProps
    const { innerWidth: winW, innerHeight: winH } = getSize()

    let x = winW / w
    let y = winH / h

    if (mode === AdaptMode.宽度铺满) {
      y = x
    } else if (mode === AdaptMode.高度铺满) {
      x = y
    }

    if (winW !== w || winH / h) {
      if (reverse) {
        setStyle({
          ...style,
          width: winW + 'px',
          height: winH + 'px',
          transform: `scale(${1 / x},${1 / y + 0.0001})`,
        })
      } else {
        setStyle({
          ...style,
          width: w + 'px',
          height: h + 'px',
          transform: `scale(${x},${y + 0.0001})`,
        })
      }
    } else {
      setStyle({
        ...style,
        width: '100%',
        height: '100%',
        transform: '',
      })
    }
  }, 100)

  useEffect(() => {
    // 先调用一次
    handleResize()
    setInit(true)
    window.addEventListener('resize', handleResize)

    return () => {
      window.removeEventListener('resize', handleResize)
    }
  }, [])

  return (
    <div ref={wrpRef} style={style}>
      {init && newProps.children}
    </div>
  )
}
